<template>
  <form @submit="formSubmit">
    <CardTitle title="General" icon="fas fa-cube"></CardTitle>
    <InputField label="App Name (app_name)" placeholder="app name" v-model="app_name" :errorMessage="errors.app_name">
    </InputField>

    <InputField label="Footer Text (footer_text)" placeholder="footer text" v-model="footer_text" :errorMessage="errors.footer_text"></InputField>
    <InputField label="Copyright Text (copyright_text)" placeholder="Copyright Text" v-model="copyright_text" :errorMessage="errors.copyright_text"></InputField>
    <InputField label="Helpline Number (helpline_number)" placeholder="Helpline Number" v-model="helpline_number" :errorMessage="errors.helpline_number"></InputField>

    <InputField label="Inquiry Email (inquriy_email)" placeholder="Inquiry Email" v-model="inquriy_email" :errorMessage="errors.inquriy_email"></InputField>
    <InputField label="Site Description (site_description)" placeholder="Site Description" v-model="site_description" :errorMessage="errors.site_description"></InputField>

    <div class="form-group mb-3 ">
      <div class="checkbox">
        <CheckboxInput label=" Show Copyright"></CheckboxInput>
      </div>
    </div>
    <div class="col row">
      <div class="form-group mb-3 col-md-6">
        <label for="logo" class="form-label"> <strong>Logo</strong> (logo)</label>
        <div class="row">
          <div class="col-lg-4">
            <div class="card text-center inline-block">
              <div class="card-body ">
                <img :src="logoViewer || DEFAULT_LOGO" class="img-fluid" alt="logo" />
              </div>
            </div>
          </div>
          <div class="col-lg-8">
            <div class="d-flex align-items-center gap-2">
              <input type="file" ref="logoInputRef" class="form-control d-none" id="logo" name="logo"
                accept=".jpeg, .jpg, .png, .gif" @change="changeLogo" />
              <label class="btn btn-info" for="logo">Upload</label>
              <input type="button" class="btn btn-danger" name="remove" value="Remove" @click="removeLogo()"
                v-if="logo" />
            </div>
            <span class="text-danger">{{ errors.logo }}</span>
          </div>
        </div>
      </div>

      <div class="form-group mb-3 col-md-6">
        <label for="logo" class="form-label"> <strong>Mini Logo</strong> (mini_logo) </label>
        <div class="row">
          <div class="col-lg-4">
            <div class="card text-center inline-block">
              <div class="card-body ">
                <img :src="miniLogoViewer || DEFAULT_MINI_LOGO" class="img-fluid" alt="mini_logo" />
              </div>
            </div>
          </div>
          <div class="col-lg-8">
            <div class="d-flex align-items-center gap-2">
              <input type="file" ref="logoMiniInputRef" class="form-control d-none" id="mini-logo" name="mini_logo"
                accept=".jpeg, .jpg, .png, .gif" @change="changeMiniLogo" />
              <label class="btn btn-info" for="mini-logo">Upload</label>
              <input type="button" class="btn btn-danger" name="remove" value="Remove" @click="removeMiniLogo()"
                v-if="mini_logo" />
            </div>
            <span class="text-danger">{{ errors.mini_logo }}</span>
          </div>
        </div>
      </div>


      <div class="form-group mb-3 col-md-6 ">
        <label for="logo" class="form-label"> <strong>Dark Logo</strong> (dark_logo) </label>
        <div class="row">
          <div class="col-lg-4">
            <div class="card text-center inline-block">
              <div class="card-body bg-dark">
                <img :src="darkLogoViewer || DEFAULT_DARK_LOGO" class="img-fluid" alt="dark_logo" />
              </div>
            </div>
          </div>
          <div class="col-lg-8">
            <div class="d-flex align-items-center gap-2">
              <input type="file" ref="darkLogoInputRef" class="form-control d-none" id="dark-logo" name="dark_logo"
                accept=".jpeg, .jpg, .png, .gif" @change="changeDarkLogo" />
              <label class="btn btn-info" for="dark-logo">Upload</label>
              <input type="button" class="btn btn-danger" name="remove" value="Remove" @click="removeDarkLogo()"
                v-if="dark_logo" />
            </div>
            <span class="text-danger">{{ errors.dark_logo }}</span>
          </div>
        </div>
      </div>

      <div class="form-group mb-3 col-md-6 ">
        <label for="logo" class="form-label"> <strong>Dark Mini Logo</strong> (dark_mini_logo) </label>
        <div class="row">
          <div class="col-lg-4">
            <div class="card text-center inline-block">
              <div class="card-body bg-dark">
                <img :src="darkMiniLogoViewer || DEFAULT_DARK_MINI_LOGO" class="img-fluid" alt="dark_mini_logo" />
              </div>
            </div>
          </div>
          <div class="col-lg-8">
            <div class="d-flex align-items-center gap-2">
              <input type="file" ref="darkLogoMiniInputRef" class="form-control d-none" id="dark-mini-logo"
                name="dark_mini_logo" accept=".jpeg, .jpg, .png, .gif" @change="changeDarkMiniLogo" />
              <label class="btn btn-info" for="dark-mini-logo">Upload</label>
              <input type="button" class="btn btn-danger" name="remove" value="Remove" @click="removeDarkMiniLogo()"
                v-if="dark_mini_logo" />
            </div>
            <span class="text-danger">{{ errors.dark_mini_logo }}</span>
          </div>
        </div>
      </div>

      <div class="form-group mb-3 col-md-6 ">
        <label for="logo" class="form-label"> <strong>Favicon</strong> (favicon)</label>
        <div class="row">
          <div class="col-lg-4 r-0">
            <div class="card text-center inline-block">
              <div class="card-body ">
                <img :src="faviconViewer || DEFAULT_FAVICON" class="img-fluid" alt="favicon" />
              </div>
            </div>
          </div>
          <div class="col-lg-8">
            <div class="d-flex align-items-center gap-2">
              <input type="file" ref="faviconInputRef" class="form-control d-none" id="favicon-logo" name="favicon"
                accept=".jpeg, .jpg, .png, .gif" @change="changeFavicon" />
              <label class="btn btn-info" for="favicon-logo">Upload</label>
              <input type="button" class="btn btn-danger" name="remove" value="Remove" @click="removeFavicon()"
                v-if="favicon" />
            </div>
            <span class="text-danger">{{ errors.favicon }}</span>
          </div>
        </div>
      </div>

      <div class="form-group mb-3 col-md-6 ">
        <label for="logo" class="form-label"> <strong>Loader</strong> (loader)</label>
        <div class="row">
          <div class="col-lg-4">
            <div class="card text-center inline-block">
              <div class="card-body ">
                <img :src="loaderViewer || DEFAULT_LOADER" class="img-fluid" alt="loader" />
              </div>
            </div>
          </div>
          <div class="col-lg-8">
            <div class="d-flex align-items-center gap-2">
              <input type="file" ref="loaderInputRef" class="form-control d-none" id="loader-logo" name="loader"
                accept=".jpeg, .jpg, .png, .gif" @change="changeLoader" />
              <label class="btn btn-info" for="loader-logo">Upload</label>
              <input type="button" class="btn btn-danger" name="remove" value="Remove" @click="removeLoader()"
                v-if="loader" />
            </div>
            <span class="text-danger">{{ errors.loader }}</span>
          </div>
        </div>
      </div>
    </div>
    <div>
      <button :disabled="IS_SUBMITED" class="btn btn-success" name="submit">
        <template v-if="IS_SUBMITED">
          <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
          Loading...
        </template>
        <template v-else> <i class="fa-solid fa-floppy-disk"></i> Submit</template>
      </button>
    </div>
  </form>
</template>

<script setup>

import CardTitle from '@/Setting/Components/CardTitle.vue'
import InputField from '@/vue/components/form-elements/InputField.vue'
import { onMounted, ref } from 'vue'
import CheckboxInput from '@/Setting/Components/CheckboxInput.vue'
import { useField, useForm } from 'vee-validate'
import { STORE_URL, GET_URL } from '@/vue/constants/setting'

import { readFile } from '@/helpers/utilities'
import { createRequest } from '@/helpers/utilities'
import * as yup from 'yup';
import { useRequest } from '@/helpers/hooks/useCrudOpration'
import FlatPickr from 'vue-flatpickr-component'

const config = ref({
  dateFormat: 'H:i',
  time_24hr: true,
  enableTime: true,
  noCalendar: true,
  defaultHour: '00', // Update default hour to 9
  defaultMinute: '30',
})

const DEFAULT_LOGO = document.querySelector('[name="logo"]').value
const DEFAULT_MINI_LOGO = document.querySelector('[name="mini-logo"]').value
const DEFAULT_DARK_LOGO = document.querySelector('[name="dark-logo"]').value
const DEFAULT_DARK_MINI_LOGO = document.querySelector('[name="dark-mini-logo"]').value
const DEFAULT_FAVICON = document.querySelector('[name="favicon"]').value
const DEFAULT_LOADER = document.querySelector('[name="loader"]').value
const IS_SUBMITED = ref(false)

const { storeRequest } = useRequest()

let logoInputRef = ref(null)
let logoMiniInputRef = ref(null)
let darkLogoInputRef = ref(null)
let darkLogoMiniInputRef = ref(null)
let faviconInputRef = ref(null)
let loaderInputRef = ref(null)

const fileUpload = async (e, { imageViewerBS64, changeFile }) => {
  let file = e.target.files[0]
  await readFile(file, (fileB64) => {
    imageViewerBS64.value = fileB64

    logoInputRef.value.value = '';
    logoMiniInputRef.value.value = '';
    darkLogoInputRef.value.value = '';
    darkLogoMiniInputRef.value.value = '';
    faviconInputRef.value.value = '';
    loaderInputRef.value.value = '';
  })
  changeFile.value = file
}
// Function to delete Images
const removeImage = ({ imageViewerBS64, changeFile }) => {
  imageViewerBS64.value = null
  changeFile.value = null
}

const logoViewer = ref(DEFAULT_LOGO)
const changeLogo = (e) => fileUpload(e, { imageViewerBS64: logoViewer, changeFile: logo })
const removeLogo = () => removeImage({ imageViewerBS64: logoViewer, changeFile: logo })

const miniLogoViewer = ref(DEFAULT_MINI_LOGO)
const changeMiniLogo = (e) => fileUpload(e, { imageViewerBS64: miniLogoViewer, changeFile: mini_logo })
const removeMiniLogo = () => removeImage({ imageViewerBS64: miniLogoViewer, changeFile: mini_logo })

const darkLogoViewer = ref(DEFAULT_DARK_LOGO)
const changeDarkLogo = (e) => fileUpload(e, { imageViewerBS64: darkLogoViewer, changeFile: dark_logo })
const removeDarkLogo = () => removeImage({ imageViewerBS64: darkLogoViewer, changeFile: dark_logo })

const darkMiniLogoViewer = ref(DEFAULT_DARK_MINI_LOGO)
const changeDarkMiniLogo = (e) => fileUpload(e, { imageViewerBS64: darkMiniLogoViewer, changeFile: dark_mini_logo })
const removeDarkMiniLogo = () => removeImage({ imageViewerBS64: darkMiniLogoViewer, changeFile: dark_mini_logo })

const faviconViewer = ref(DEFAULT_FAVICON)
const changeFavicon = (e) => fileUpload(e, { imageViewerBS64: faviconViewer, changeFile: favicon })
const removeFavicon = () => removeImage({ imageViewerBS64: faviconViewer, changeFile: favicon })

const loaderViewer = ref(DEFAULT_LOADER)
const changeLoader = (e) => fileUpload(e, { imageViewerBS64: loaderViewer, changeFile: loader })
const removeLoader = () => removeImage({ imageViewerBS64: loaderViewer, changeFile: loader })

//  Reset Form
const setFormData = (data) => {
  (logoViewer.value = data.logo),
    (miniLogoViewer.value = data.mini_logo),
    (darkLogoViewer.value = data.dark_logo),
    (darkMiniLogoViewer.value = data.dark_mini_logo),
    (faviconViewer.value = data.favicon),
    (loaderViewer.value = data.loader),

    resetForm({
      values: {
        app_name: data.app_name,
        footer_text: data.footer_text,
        copyright_text: data.copyright_text,
        helpline_number: data.helpline_number,
        inquriy_email: data.inquriy_email,
        site_description: data.site_description,
        logo: '',
        mini_logo: '',
        dark_logo: '',
        dark_mini_logo: '',
        favicon: '',
        loader: ''
      }
    })
}

const numberRegex = /^\d+$/;

const validationSchema = yup.object({
  app_name: yup.string({ required_error: 'App name is required' }),
  footer_text: yup.string({ required_error: 'Footer text is required' }),
  helpline_number: yup.string()
    .matches(/^\d+$/, 'Only numbers are allowed'),
  copyright_text: yup.string({ required_error: 'Copyright text is required' }),
  inquriy_email: yup.string().test('is-string', 'First strings are allowed', (value) => !numberRegex.test(value)).email('must be a valid email'),
  site_description: yup.string({ required_error: 'site description is required' }),

  // logo: yup.any().refine(
  //   (file) => {
  //     // Check if all items in the array are instances of the File object
  //     return file instanceof File;
  //   },
  //   {
  //     // If the refinement fails, throw an error with this message
  //     message: 'Expected a file',
  //   },
  // )
})

const { handleSubmit, errors, resetForm } = useForm({ validationSchema })
const errorMessage = ref(null)
const { value: app_name } = useField('app_name')
const { value: footer_text } = useField('footer_text')
const { value: helpline_number } = useField('helpline_number')
const { value: copyright_text } = useField('copyright_text')
const { value: inquriy_email } = useField('inquriy_email')
const { value: site_description } = useField('site_description')
const { value: logo } = useField('logo')
const { value: mini_logo } = useField('mini_logo')
const { value: dark_logo } = useField('dark_logo')
const { value: dark_mini_logo } = useField('dark_mini_logo')
const { value: favicon } = useField('favicon')
const { value: loader } = useField('loader')
//fetch data
const data = 'app_name,footer_text,helpline_number,copyright_text,inquriy_email,site_description,logo,mini_logo,dark_logo,dark_mini_logo,favicon,loader'
onMounted(() => {
  createRequest(GET_URL(data)).then((response) => {
    setFormData(response)
  })
})

// message
const display_submit_message = (res) => {
  IS_SUBMITED.value = false;
  if (res.status) {
    window.successSnackbar(res.message)
  } else {
    window.errorSnackbar(res.message)
  }
}

//Form Submit
const formSubmit = handleSubmit((values) => {
  IS_SUBMITED.value = true;
  storeRequest({ url: STORE_URL, body: values, type: 'file' }).then((res) => display_submit_message(res))
})
</script>
